.btn{
    padding: 1rem 2rem;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: fit-content;
    margin-top: 1.5rem;
}

.outlined{
    background-color: transparent;
    border: 1px solid var(--pink1);
    color: var(--pink1);
    font-size: 1rem;
}

.btn:hover{
    background-color: #ff567843;
    border: 1px solid var(--pink1);

}